@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700&family=Poppins:wght@400;500;600;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.leftkyc {
    position: relative;
    max-height:100%;
    padding: 0;
    background-color: #fff;
    border-bottom-left-radius: 10px;
    border-top-left-radius: 10px;
}
.rightkyc {
    padding:0 0 250px 0;
    background-color: #e5e5e5;
    border-bottom-right-radius: 10px;
    border-top-right-radius: 10px;
}
/* kyc section  */
.kyc-section {
    padding: 40px 7px;
    height:100%;
    background-color: #d1cece;
}
/* leftside */
.leftkycside {
    position: absolute;
    top: 13px;
    left: 0;
    width: 100%;
}

.leftkycside > h1 {
    font-size: 1.5rem;
    font-weight: 400;
    color: #4c5565;
    margin-top: 30px;
    padding-left: 10px;
}
.leftkycside > p {
    font-size: 0.8rem;
    padding-left: 10px;
}
.contact {
    background-color: #2b83ea;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.contact > p {
    color: #fff;
    font-size: 1rem;
    margin-top: 13px;
    padding-left: 10px;
}
.contact > .greater {
    color: #fff;
    font-weight: 200;
    padding-right: 5px;
}
.list > p {
    padding-left: 10px;
    margin-top: 10px;
}

/* right side */

.flexing {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 38px;
}
.flexing > p {
    text-transform: uppercase;
    font-weight: 500;
    padding-left: 20px;
    color: #4C5565;

}
.flexing > img {
    cursor: pointer;
    margin-bottom:20px;
}
.personal {
    margin: 0 auto;
    width: 80%;
    text-align: center;
}
.labelone > label,
.labeltwo > label,
.labelthree > label {
    color:#4C5565;
    margin-top: 30px;
    font-size: 1.2rem;
    font-family:Poppins;
}
.labelone > input {
    margin-left: 25px;
}
.labelone>input,.labeltwo>input,.labelthree>input{
    height:40px;
    width: 35%;
    border: 0.5px solid #000;
    border-radius: 5px;
    border: 1px solid rgba(0, 0, 0, 0.25);
}
.labeltwo > input {
    margin-left: 10px;
}
.labelthree > input {
    margin-left: 25px;
}

/* bottom */

.bottomkyc{
    padding-top:30px;
    display:flex;
    justify-content: flex-end;
    align-items:center;
    border-top:1px solid #4C5565;
    position:relative;
    top:220px;
}

.bottomkyc button:nth-child(1){
    padding:5px 15px;
margin-right:10px;
color:#2C3539;
border:1px solid #d9dddd;
border-radius: 5px;
}
.bottomkyc button:nth-child(2){
    padding:5px 15px;
    color:#ffffff;
    border:1px solid #d9dddd;
    border-radius:5px;
    background-color: #2B83EA;
    
}
@media only screen and (max-width:576px){
    .leftkycside {
        position: unset;
        
    }
    
.labelone>input,.labeltwo>input,.labelthree>input{
    height:30px;
    width:50%;
}
.labelone > label,
.labeltwo > label,
.labelthree > label {
    font-size: 0.8rem;
    font-family:Poppins;
}
}
@media only screen and (max-width:768px){
    .leftkycside {
        position: unset;
        
    }
    

}



* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Poppins;
}
.otp-section {
    background-color: #23304c;
    width: 100%;
    height: auto;
    padding: 30px 20px;
}
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 50px 0;
}
.logo img {
    width: 80%;
    margin-bottom:20px;
}
.login {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}
.login p {
    color: #fff;
    margin-right: 10px;
}
.login p:nth-child(2) {
    background-color: #2b83ea;
}
/* form style */

.left {
    color: #fff;
    width: 100%;
}
.right {
    color: #f4f4f4;
    width: 100%;
}
.form-input {
    padding: 50px 0;
    height: auto;
}
.lefty {
    background-color: #fff;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    box-shadow: 5px 0px 10px 0.5px rgba(0, 0, 0, 0.46);
    z-index: 100;
    position: relative;
}
.righty {
    background-color: #fafafa;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
}
.form-input h1 {
    text-align: center;
    color: #444444;
    font-size: 1rem;
    font-weight: 400;
}
.input-field {
    padding: 20px 50px;
}
.eye-icon {
    border-bottom: 1px solid #859fc1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin-top: 20px;
}
.eye-icon input {
    border: none;
    height: 27px;
    width: 100%;
    outline: none;
    background-color: transparent;
}
.eye-icon .eye {
    color: #859fc1;
}
.eye-icon input:-ms-input-placeholder {
    color: #859fc1;
    font-size: 0.8rem;
}
.eye-icon input::placeholder {
    color: #859fc1;
    font-size: 0.8rem;
}
.check-box {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.check-box p {
    font-size: 0.7rem;
    margin-left: 10px;
    margin-top: 15px;
}
.check-box > .check {
    color: #2b83ea;
    font-size:2rem;
}
.verify-btn > button {
    background-color: #2b83ea;
    width: 100px;
    font-size: 0.9rem;
    color: #fff;
    border: none;
    border-radius: 5px;
    padding: 9px 5px;
}
.agree {
    margin-top: 10px;
}
.agree > p {
    font-size: 0.7rem;
    margin-top:15px;
}

/* margin-seting */

/* rightside style */
.rightside {
    text-align: center;
    padding: 50px 0;

    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
}
.top {
    margin-bottom: 40px;
}
.top > h4 {
    color: #444444;
}
.top > p {
    color: #4c5565d9;
}
.rightimg {
    width: 100%;
}
.rightside img {
    width: 50%;
}
.bottom > p {
    color: #4c5565d9;
    margin-top: 50px;
}

@media only screen and (max-width: 576px) {
    .logo img {
        width: 70%;
    }
    .login p {
        font-size: 0.8rem;
        width: 100px;
    }
    .login p:nth-child(2) {
        font-size: 0.8rem;
        width: 100px;

        background-color: #2b83ea;
    }
    .lefty {
      box-shadow: 0 0 0 0 ;
      border-top-left-radius: 5px;
      border-top-right-radius:5px;
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
    }
    .righty {
        border-top-left-radius: 0px;
        border-top-right-radius:0px;
        border-bottom-left-radius: 10px;
        border-bottom-right-radius: 10px;
      
    }
}

